import React from "react";
import { Dropdown, Button, message } from "antd";
import {
  DownOutlined,
  CheckCircleTwoTone,
  CloseCircleTwoTone,
} from "@ant-design/icons";

const MarkButton = ({ loading, handleClick, todayStatus }) => {
  const handleMenuClick = ({ key, keyPath, domEvent }) => {
    message.info("Saved");
    console.log("click", key);
    handleClick(key);
  };
  console.log("today status from button comp", todayStatus);
  const status = todayStatus === undefined ? null : todayStatus.status;

  const items = [
    {
      label: "洗过了",
      key: "洗过了",
      disabled: status === "洗过了",
    },
    {
      label: "不洗了",
      key: "不洗了",
      disabled: status === "不洗了",
    },
  ];

  const menuProps = {
    items,
    onClick: handleMenuClick,
  };

  return (
    <Dropdown menu={menuProps} loading={loading} trigger={["click"]}>
      <Button type={status ? "default" : "primary"} shape="round" size="large">
        {status === "洗过了" && <CheckCircleTwoTone />}
        {status === "不洗了" && <CloseCircleTwoTone twoToneColor="#ff0000" />}
        我今天{status || "..."}
        <DownOutlined />
      </Button>
    </Dropdown>
  );
};

export default MarkButton;
