import "./styles.css";
import React, { useState, useEffect } from "react";
import moment from "moment";
import carWashAnimated from "./assets/car-wash.gif";
import carWashStatic from "./assets/car-wash.png";
import { Col, Row, Flex, Typography, Badge } from "antd";
const { Title } = Typography;
import firestore from "./firebase";
import { collection, getDocs, doc, setDoc } from "firebase/firestore";
import MarkButton from "./components/MarkButton";

export default function App() {
  const [allStatus, setAllStatus] = useState([]);
  const [loading, setLoading] = useState(false);

  const startDate = moment().startOf("week");
  const endDate = moment("2024-03-05");
  const arrangementStart = {
    date: moment("2023-12-07"),
    peopleIndex: 0,
  };
  const people = ["Henry", "Ken", "Leo", "Nana", "Vivian"];
  const totalViews = [];
  let weekViews = [];
  let todayPerson;
  while (startDate.isBefore(moment(endDate).endOf("week"))) {
    // const currentDate = startDate.add(1, "days");
    const today = {
      date: startDate.format("YYYY-MM-DD"),
      dayISO: startDate.format("ddd"),
      day: startDate.day(),
      people: startDate.isBefore(moment(endDate))
        ? people[startDate.diff(arrangementStart.date, "day") % 5]
        : null,
    };
    if (moment(today.date).isSame(new Date(), "day")) {
      todayPerson = today.people;
    }
    weekViews.push(today);
    if (today.day === 6) {
      totalViews.push(weekViews);
      weekViews = [];
    }
    startDate.add(1, "days");
  }

  useEffect(() => {
    // Example: Fetch data from Firestore
    const fetchData = async () => {
      try {
        const querySnapshot = await getDocs(
          collection(firestore, "calendarMark")
        );
        const newData = [];
        querySnapshot.forEach((doc) => {
          newData.push({ id: doc.id, ...doc.data() });
        });
        setAllStatus(newData);
        console.log("data from fb", newData);
      } catch (error) {
        console.error("Error fetching data: ", error);
      }
    };

    fetchData();
  }, []);

  const updateData = (newObject) => {
    setAllStatus((prevData) => {
      const itemIndex = prevData.findIndex((item) => item.id === newObject.id);

      if (itemIndex !== -1) {
        // If the item exists, update it
        return prevData.map((item, index) =>
          index === itemIndex ? { ...newObject } : item
        );
      } else {
        // If the item doesn't exist, add it to the array
        return [...prevData, newObject];
      }
    });
  };

  const addDataToFirestore = async (clickedOption) => {
    try {
      setLoading(true);
      const docRefID = moment().format("YYYYMMDD");
      const data = {
        date: moment().format("YYYY-MM-DD"),
        person: todayPerson,
        status: clickedOption,
      };
      // Add the data to Firestore
      const docRef = doc(collection(firestore, "calendarMark"), docRefID);
      await setDoc(docRef, data);
      updateData({ id: docRefID, ...data });
      console.log("Document written with ID: ", docRef.id);
    } catch (error) {
      console.error("Error adding document: ", error);
    } finally {
      setLoading(false);
    }
  };

  const todayStatus = allStatus.find(
    (s) => s.id === moment().format("YYYYMMDD")
  );

  return (
    <div className="App">
      <Row align="middle">
        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
          <img src={carWashAnimated} style={{ width: "100%" }} />
        </Col>
        <Col xs={24} sm={24} md={24} lg={12} xl={12}>
          <Badge.Ribbon text={moment().format("LL")}>
            <Flex
              vertical={"vertical"}
              justify="center"
              align="center"
              style={{ minHeight: "280px" }}
            >
              <Title level={4}>今天洗车的是</Title>
              <Title>{todayPerson}</Title>
              <MarkButton
                loading={loading}
                handleClick={addDataToFirestore}
                todayStatus={todayStatus}
              />
            </Flex>
          </Badge.Ribbon>
        </Col>
      </Row>
      <h3></h3>
      <table>
        <thead>
          <tr>
            <th colSpan={7}>
              Days remaining: {Math.abs(moment().diff(endDate, "days"))}
            </th>
          </tr>
          <tr>
            <th>Sun</th>
            <th>Mon</th>
            <th>Tue</th>
            <th>Wed</th>
            <th>Thu</th>
            <th>Fri</th>
            <th>Sat</th>
          </tr>
        </thead>
        <tbody>
          {totalViews.map((week, i) => (
            <tr key={i}>
              {week.map((day, j) => (
                <td
                  key={j}
                  className={
                    moment(day.date).isSame(moment(), "day")
                      ? "active"
                      : moment(day.date).isBefore(moment())
                      ? "past"
                      : "future"
                  }
                >
                  <div className="day">
                    <div className="header">
                      {moment(day.date).format("MMM DD")}
                    </div>
                    <div className="icon">
                      <img src={carWashStatic} />
                    </div>
                    <div className="people">{day.people}</div>
                  </div>
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
